  .page-header__logo {
    width: 147px;
    height: 40px;
    margin: 27px 20px 43px 20px;
  }
  .page-header__logo .page-header__logo-image {
    display: none;
  }
  .page-header__logo .page-header__logo-desktop {
    display: none;
  }
  .page-header__logo svg {
    width: 147px;
    height: 40px;
  }
  .page-header__logo .page-header__logo-tablet {
    display: inline-block;
  }
